<template>
  <div>
    <mobile-header title="상조 변경" class="mb-6" @close="backRouting" />
    <div id="m_admin-main__page" class="pt-0">
      <section class="sidebar-items__wraps corp-change">
        <button class="corp-change__button" @click="move('/corp/regist')">
          <v-icon color="#fff">mdi-plus-circle-outline</v-icon>
          소속상조 추가
        </button>

        <div class="preview-box">
          <v-icon>mdi-home-city-outline</v-icon>
          <p @click="changeCorp(memberInfo.memberCorpName)" class="mb-0">
            {{ memberInfo.memberCorpName }}
          </p>
        </div>
      </section>
    </div>

    <corp-change-modal
      v-if="changeFlag"
      @cancel="cancel"
      :corpName="corpName"
    />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import MobileHeader from "@/components/common/MobileHeader.vue";
import CorpChangeModal from "@/components/common/CorpChangeModal.vue";
export default {
  components: {
    MobileHeader,
    CorpChangeModal
  },
  data() {
    return {
      changeFlag: false,
      corpName: null
    };
  },

  computed: {
    ...mapGetters(["memberInfo"])
  },
  methods: {
    backRouting() {
      this.$router.go(-1);
    },

    move(path) {
      this.$router.push(path);
    },

    changeCorp(corpName) {
      // return alert("서비스 준비중 입니다");
      this.changeFlag = true;
      this.corpName = corpName;
    },
    cancel() {
      this.changeFlag = false;
    }
  }
};
</script>

